	module.exports = {"data":{"markdownRemark":{"html":"<h2>Area of a Rectangle</h2>\n<p>The area of a rectangle can be calculated by multiplying the lengths of the two perpendicular sides together.</p>\n<p><code>Area = a x b</code></p>\n<p><strong>For Example</strong>\nFor a rectangle where <em>a = 3</em> and <em>b = 4</em></p>\n<p><code>Area = a x b = 3 x 4 = 12</code></p>\n<h4>More Information:</h4>\n<ul>\n<li><a href=\"http://virtualnerd.com/pre-algebra/perimeter-area-volume/perimeter-and-area/area-formulas-examples/rectangle-area-example\">Virtual Nerd</a></li>\n</ul>","fields":{"slug":"/mathematics/area-of-a-rectangle/"},"frontmatter":{"title":"Area of a Rectangle"}}},"pathContext":{"slug":"/mathematics/area-of-a-rectangle/"}}

